<template>
    <div class="main-container">
        <HeaderView />
        <div class="content-area">
            <div class="left-area">
                <LeftView @change-view="changeView" />
            </div>
            <div class="right-area">
                <component :is="currentView" />
            </div>
        </div>
    </div>
</template>

<script setup>
import { shallowRef } from "vue";
import HeaderView from "@/components/HeaderView.vue";
import LeftView from "@/components/LeftMenu.vue";
import Menu1View from "@/components/Menu1View.vue";
import Menu2View from "@/components/Menu2View.vue";
import Menu3View from "@/components/Menu3View.vue";

const viewMap = {
    Menu1View,
    Menu2View,
    Menu3View,
};

// 초기값 설정
const currentView = shallowRef(Menu1View);

// changeView 함수 수정: viewName이 실제로 컴포넌트 이름이므로, viewMap에서 해당 컴포넌트를 찾아서 할당
function changeView(viewName) {
    currentView.value = viewMap[viewName] || Menu1View;
}
</script>

<style scoped>
.main-container {
    display: flex;
    flex-direction: column;
    height: 100vh;
}

.content-area {
    display: flex;
    flex: 1;
}

.left-area {
    width: 150px;
    background-color: #f8f8f8;
    border-right: 1px solid #ddd;
}

.right-area {
    width: 90%;
    padding: 20px;
    overflow-y: auto;
}
</style>
